/*Mixins*/

@mixin mq($bp) {
    @if map-has-key($breakpoints, $bp) {
        @media screen and (min-width: map-get($breakpoints, $bp)) {
            @content;
        }
    }@else {
        @media screen and (min-width: $bp) {
            @content;
        }
    }
    
}

@mixin clearMenu {
  list-style    : none;
  margin-top    : 0;
  margin-bottom : 0;
  padding-left  : 0;
  margin-left   : 0;
}

@mixin widget {
  box-shadow: 0 4px 15px -2px rgba(255, 255, 255, 0.767);
  border : none;
  background : #fff;
}

@mixin round {
  border-radius : 0.25rem;
}

@mixin radius {
  border-radius : 1.5rem;
}

@mixin circle {
  border-radius : 50%;
}

@mixin profile-photo {
  @include round;
  flex       : none;
  height     : 3rem;
  width      : 3rem;
  object-fit : cover;
}
